@media (width: 1024px) {
  .secondScreen {
    width: 1024px;
    height: 768px;
    padding: 167px 133px 83px 69px;

    &__title {
      color: #242424;
      font-size: 27px;
      font-family: "DIN Pro", serif;
      font-weight: 400;
      text-transform: uppercase;
      line-height: 39px;
      z-index: 5;
    }

    &__descr {
      position: relative;
      padding-left: 30px;
      display: flex;
      justify-content: space-between;
      margin-top: 15px;
      width: 830px;
      height: 450px;
      background-color: transparent;
      z-index: 5;

      &__layer {
        position: absolute;
        top: 0;
        left: 53px;
        width: 775px;
        height: 445px;
        box-shadow: 3px 4px 116px 2px rgba(157, 185, 198, 0.2);
        border-radius: 40px;
        background-color: #ffffff;
      }

      &__scroll {
        position: absolute;
        top: 20px;
        left: 15px;
        margin-left: 7px;
        margin-top: 25px;
        width: 6px;
        height: 350px;
        border-radius: 3px;
        background-color: #000000;
      }

      &__text {
        p {
          direction: ltr;
          font-size: 21px;
          line-height: 33px;
          font-family: "Gilroy", serif;

          span {
            font-family: "Gilroy", serif;
            font-weight: 900;
          }
        }

        width: 830px;
        padding: 30px 30px 35px 80px;
        margin-left: -15px;
        direction: ltr;
        z-index: 10;
        overflow: scroll;
        direction: rtl;

        &::-webkit-scrollbar {
          width: 20px;
        }

        &::-webkit-scrollbar-corner {
          width: 0;
        }

        &::-webkit-scrollbar-thumb {
          background: url('../../img/screen_2/scroll_btn.png') center center no-repeat;
          object-fit: contain;
          width: 20px;
          height: 104px;
          z-index: 10;
        }
      }
    }
  }
}