@media (width: 1024px) {
  .bottle {
    position: absolute;
    top: 80px;
    left: 50px;
    width: 233px;
    height: 689px;
    z-index: 5;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .modal-background {
    background-color: rgba(0, 0, 0, 0.5);
    width: 1024px;
    height: 768px;
    padding: 121px 60px 60px 141px;

    position: relative;
  }

  .modal {
    width: 823px;
    height: 573px;
    padding: 62px 132px 15px 120px;
    background-color: #fff;
    border-radius: 40px;

    &__window {
      display: block;
      position: relative;
      padding-left: 113px;

      &-header {
        &_subtitle {
          color: #242424;
          font-size: 27px;
          text-transform: uppercase;
          font-family: "DIN Pro", serif;
          font-weight: 400;
          margin: -23px 0 15px;
        }

        &_title {
          color: #242424;
          font-size: 90px;
          line-height: 100px;
          font-family: "Gilroy", serif;
          font-weight: 400;
          letter-spacing: 4px;

          span {
            font-family: "Gilroy", serif;
            font-weight: 900;
          }
        }
      }

      &-close {
        width: 34px;
        height: 35px;
        position: absolute;
        top: -20px;
        right: -100px;
        border: none;
        background: none;

        img {
          width: 100%;
          height: 100%;
          margin-top: 11px;
        }
      }
    }
  }

  &__cards {
    margin-top: 77px;
    display: flex;
    justify-content: space-between;
    width: 742px;
    height: 265px;
    z-index: 1;
  }

  &__card_left {
    position: relative;
    padding: 54px 50px 43px 130px;
    min-width: 414px;
    height: 265px;
    box-shadow: 3px 4px 116px 9px rgba(157, 185, 198, 0.39);
    border-radius: 40px;
    background-color: #ffffff;
    font-size: 23px;
    font-family: "DIN Pro", serif;
    font-weight: 400;

    &:before {
      content: "";
      width: 68px;
      height: 63px;
      background: url("../../../img/screen_3/icon1.png") center center/cover;
      position: absolute;
      top: -33px;
      left: 129px;
    }
  }

  &__card_right_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__card_right {
    position: relative;
    padding: 54px 39px 45px 44px;
    width: 263px;
    height: 150px;
    box-shadow: 3px 4px 116px 9px rgba(157, 185, 198, 0.39);
    border-radius: 40px;
    background-color: #ffffff;
    font-size: 23px;
    font-family: "DIN Pro", serif;
    font-weight: 400;

    &:before {
      content: "";
      width: 70px;
      height: 58px;
      background: url("../../../img/screen_3/icon2.png") center center/cover;
      position: absolute;
      top: -25px;
      left: 40px;
    }
  }

  &__button {
    margin: 36px;
    width: 289px;
    height: 81px;
    border: none;
    cursor: pointer;
    background: none;

    img {
      width: 100%;
      height: 100%;
    }
  }
}